@import url(https://fonts.googleapis.com/css?family=Merriweather);
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,300);
@import url(https://fonts.googleapis.com/css?family=Noto+Serif:400);

.language-list {
    list-style: none;
    margin-right: auto;
  }
  
  .lang, .lang-active {
    float: left;
  }
  
  .nav-list {
    list-style: none;
    margin-left: auto;
  }
  .nav, .nav-active {
    float: right;
  }
  .btn-nav, .btn-lang {
    /* background-color: rgba(72, 68, 89, 0.2); */
    color: #222;
    text-decoration: none;
    border: none;
    padding: 0 1em;
    height: 2em;
    line-height: 2em;
    outline: none
  }
  .btn-nav a, .btn-lang a {
    text-decoration: none;
    color: #222;
    outline: none;
  }
  .btn-nav:hover, .btn-nav a:hover, 
  .btn-nav:active, .btn-nav a:active,
  .btn-nav:focus, .btn-nav a:focus,
  .nav-active.btn-nav, .nav-active.btn-nav a,
  .btn-lang:hover, .btn-lang a:hover,
  .btn-lang:active, .btn-lang a:active
  .btn-lang:focus, .btn-lang a:focus
  .lang-active.btn-nav, .lang-active.btn-nav a {
    /* background-color: rgba(72, 68, 89, 0.5); */
    outline: none;
    color: #7c759a;
  }
  
  /* ADDING AN ICON TO THE LANGUAGE MENU, it needs something, not sure what */
  .lang-active::before, .lang::before {
    font-family: FontAwesome;
    content: '\F1AB';
  }
  .lang-active::before {
    color: #ccc;
  }

  .language-list > ul, .nav-list > ul {
    margin: 0;
  }
#banner {
    background-size: cover;
    margin-top: 40px;
    font-family: 'Noto Serif', serif;
    color: #fff;
}
#banner h3 {
    color: #fff;
}

#banner header {
    padding: 5em 0 2em 0;
}
#header {
    position: fixed;
    background: #ebebeb;
    padding: 0.5em;
}


#footer {
    border-top: 1px solid #efefef;
    background: #222; /*url("../images/bg.jpg")*/
  }
.spotlights.col1 > section {
    width: 100%;
}

.spotlights.col1 > section:nth-child(odd) {
    border-right: none;
}
.list-stats { 
    font-size: 1.2em;
    list-style: none;
    border-left: 1px solid #999;
    margin-bottom: 3em;
}
.list-stats li { }
.list-stats li p { 
    padding:8px 0 4px 8px; 
    color: #ddd;
    margin: 0 0 1.2em 0;
}
.list-stats li p em {
    font-weight: normal;
    font-style:normal;  
    font-family: 'Merriweather';
    color: #efefef;
  }


