.sidenav {
    min-height: 100%;
    border: 1px solid #484459;
    width: 33%;
    z-index: 1;
    padding: 1em;
    margin: 1em;
    /* background-color: #111; */
  }
  
  .sidenav a {
    padding: 6px 8px 6px 16px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
  }
  
  .sidenav a:hover {
    color: #f1f1f1;
  }
  .sidenav-title {
    font-family: 'Noto-Serif', serif;
    text-transform: uppercase;
    letter-spacing: 0.1em;
    padding-bottom: 0.6em;
  }
  .meta-count {
    letter-spacing: 0.2em;
    margin: 0 0 1em 0;
    text-transform: uppercase;
    font-weight: 300;
    font-size: 0.8em;
  }
