.card {
    /* border: 1px solid #7c759a; */
    align-content: flex-start;
    padding: 10px;
    margin: 10px; /*1%;*/
    min-width: 30%;
    height: 100%;
}
.card-item-link {
  cursor: pointer;
  padding: 0.4em;
  background: transparent;
  border: 1px solid #efefef;
  -webkit-transition: background 100ms linear, border 160ms linear;
  transition: background 100ms linear, border 160ms linear;
  border-radius: 4px;
  line-height: 1.4em;
}
.card-item-link:hover {
  background: #fff;
  border: 1px solid #80799c;
  -webkit-transition: background 200ms linear, border 220ms linear;
  transition: background 200ms linear, border 220ms linear;
}
.card-item-link:before {
  content: "\F101"; /* FontAwesome Unicode */
  font-family: FontAwesome;
  display: inline-block;
  /* margin-left: -1.3em; same as padding-left set on li */
  width: 1.3em; /* same as padding-left set on li */
}
.card-sub-item {
  padding-left: 10%;
}
.item {
  margin-top: 0.6em;
}
.item-title {
  font-weight: bold;
}

.subitems {
  margin-left: 20px;
}

.subitems.card-item-link::before {
  content: "\F105"; /* FontAwesome Unicode */
  font-family: FontAwesome;
  display: inline-block;
  /* margin-left: -1.3em; same as padding-left set on li */
  width: 1.3em; /* same as padding-left set on li */
}

.item-lead {
    font-weight: bold;
}
.item-work {
  font-weight: bold;
  text-transform: uppercase;
}
.item-btn {
  text-decoration: underline;
  cursor: pointer;
}
.item-btn:hover {
  opacity: 0.8;
}

.item-work-label {
    text-transform: uppercase;
    letter-spacing: 0.1em;
}

.image-first-image-scan {
  margin: 0 1em 1em 1em;
}

.meta-detail {
  font-style: italic;
  font-size: 0.8em;
  margin: 0 0 0.5em 0;
}
.meta-detail-id {
  font-style: italic;
  font-size: 0.8em;
  margin: 0 0 0.5em 0;
  /* float: right; */
}

