/* Modal Content/Box */
.modal {
    display: none; 
    position: fixed;
    z-index: 10002; 
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%; 
    overflow: auto; 
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0,0.4);
}
  
.modal-main {
    position:fixed;
    left: calc(-50vw + 50%);
    right: calc(-50vw + 50%);
    margin-left: auto;
    margin-right: auto;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-filter: blur(0.0px);
            filter: blur(0.0px);
    background: #efefef;
    border: 1px solid #888;
    color: #484459;
    word-break: break-all;
    width: 100%;
    max-height: 100%;
    height: 100vh;
    overflow: scroll;
  
    /* top: 40%; */
    top: calc(-50vh + 50%);
    /* bottom: calc(-50vh + 60%) */
    /* TRANSFORM CAUSES FONT BLURRINESS */
    /* transform: perspective(1px) translate(-50%,-50%); */
}
.modal-main {
    padding: 2%;
}
.modal-text {
    margin: 0 0 0.5em 0;
}
.modal-btn {
    margin-right: 12px;
}
.modal-title {
    font-size: 1.6em;
    font-family: 'Noto Serif', serif;
}
.spacer {
    padding: 0 0 0 1em;
}
.all-caps {
    text-transform: uppercase;
}
.no-trans {
    text-transform: none;
}
.meta-italics {
    font-style: italic;
}  
.meta-item {
    margin: 0;
}
.meta-grouping {
    padding-bottom: 1em;
}
.with-fa {
    padding-bottom: 0.1em;
}
.with-fa-note {
    padding-bottom: 0.5em;
}
.with-fa::before, .with-fa-note::before {
    font-family: FontAwesome;
    display: inline-block;
    width: 1.3em; /* same as padding-left set on li */
}
.with-fa::before {
    content: '\F007'; /*'\f101'; */
}
.with-fa-note::before {
    content: '\F15B';
}
.meta-catalog {
    font-size: 1em;
    word-break: normal;
}
.meta-title {
    font-weight: bold;
}
.meta-detail-list {
    font-style: italic;
    font-size: 0.8em;
    margin: 0 0 0.5em 0;
    list-style-type: none;
    padding-left: 0;
    padding-bottom: 1em;
}
.detail-data {
    background-color: #efefef;
    padding: 4%;
    border: 1px solid #222;
}


