			background-image: url(data:image/png;base64,), url(/static/media/checker-pattern.9631b446.svg);
			background-position: center top;
			background-repeat: repeat, repeat;
		}

		@media screen and (max-width: 980px) {

			.wrapper.alt > .inner {
				padding: 4em 2em 1.6em 2em ;
			}

		}

		@media screen and (max-width: 736px) {

			.wrapper.alt > .inner {
				padding: 3em 2em 0.6em 2em ;
			}

		}

		@media screen and (max-width: 480px) {

			.wrapper.alt > .inner {
				padding: 2em 1.5em 0.1em 1.5em ;
			}

		}

/* Header */

	@-webkit-keyframes reveal-header {
		0% {
			top: -4em;
			opacity: 0;
		}

		100% {
			top: 0;
			opacity: 1;
		}
	}

	@keyframes reveal-header {
		0% {
			top: -4em;
			opacity: 0;
		}

		100% {
			top: 0;
			opacity: 1;
		}
	}

	#header {
		-webkit-animation: none;
		animation: none;
		display: -moz-flex;
		display: -ms-flex;
		display: flex;
		left: 0;
		position: absolute;
		top: 0;
		width: 100%;
		z-index: 10001;
		padding: 1em;
		-ms-flex-pack: justify;
	}

		#header > a {
			color: inherit;
			font-size: 0.75em;
			font-weight: 400;
			letter-spacing: 0.13em;
			height: 3em;
			line-height: 3em;
			text-decoration: none;
			text-transform: uppercase;
			margin-left: 2em;
		}

			#header > a.button {
				padding: 0 1.5em;
			}

			#header > a:first-child {
				margin-left: 0;
			}

			#header > a[href="#menu"] {
				text-decoration: none;
				margin-right: auto;
				-webkit-tap-highlight-color: transparent;
				padding-left: 0.5em;
			}

				#header > a[href="#menu"]:before {
					-moz-osx-font-smoothing: grayscale;
					-webkit-font-smoothing: antialiased;
					font-family: FontAwesome;
					font-style: normal;
					font-weight: normal;
					text-transform: none !important;
				}

				#header > a[href="#menu"]:before {
					content: '\F0C9';
					margin: 0 0.5em 0 0;
				}

		#header.alt {
			-webkit-animation: reveal-header 0.5s ease;
			animation: reveal-header 0.5s ease;
			box-shadow: 0 0 0.15em 0 rgba(0, 0, 0, 0.1);
			position: fixed;
			top: 0;
			left: 0;
			width: 100%;
			background-color: rgba(255, 255, 255, 0.95);
			padding: 0.5em;
		}

			#header.alt a[href="#menu"] {
				padding-left: 1em;
			}

	@media screen and (max-width: 980px) {

		#header > h1 {
			left: 1em;
		}

			#header > h1 a {
				font-size: 1em;
			}

	}

	@media screen and (max-width: 480px) {

		#header {
			padding: 0.5em;
			min-width: 320px;
		}

			#header.alt {
				padding: 0.5em;
			}

				#header.alt a[href="#menu"] {
					padding-left: 0.5em;
				}

	}

/* Menu */

	#menu {
		-webkit-transform: translateX(-100%);
		transform: translateX(-100%);
		-webkit-transition: -webkit-transform 0.5s ease, box-shadow 0.5s ease, visibility 0.5s;
		-webkit-transition: box-shadow 0.5s ease, visibility 0.5s, -webkit-transform 0.5s ease;
		transition: box-shadow 0.5s ease, visibility 0.5s, -webkit-transform 0.5s ease;
		transition: transform 0.5s ease, box-shadow 0.5s ease, visibility 0.5s;
		transition: transform 0.5s ease, box-shadow 0.5s ease, visibility 0.5s, -webkit-transform 0.5s ease;
		-webkit-overflow-scrolling: touch;
		background: #fff;
		box-shadow: none;
		height: 100%;
		left: 0;
		max-width: 80%;
		overflow-y: auto;
		padding: 3em 2em;
		position: fixed;
		top: 0;
		visibility: hidden;
		width: 20em;
		z-index: 10002;
	}

		#menu > ul {
			margin: 0 0 1.2em 0;
		}

			#menu > ul.links {
				list-style: none;
				padding: 0;
			}

				#menu > ul.links > li {
					font-weight: 400;
					letter-spacing: 0.15em;
					padding: 0;
					text-transform: uppercase;
				}

					#menu > ul.links > li > a:not(.button) {
						border: 0;
						border-top: solid 1px rgba(144, 144, 144, 0.25);
						color: inherit;
						display: block;
						line-height: 3.5em;
						text-decoration: none;
						font-size: 0.9em;
					}

					#menu > ul.links > li > .button {
						display: block;
						margin: 0.5em 0 0 0;
					}

					#menu > ul.links > li:first-child > a:not(.button) {
						border-top: 0 !important;
					}

		#menu .close {
			text-decoration: none;
			-webkit-transition: color 0.2s ease-in-out;
			transition: color 0.2s ease-in-out;
			-webkit-tap-highlight-color: transparent;
			border: 0;
			color: #999;
			cursor: pointer;
			display: block;
			height: 3.25em;
			line-height: 3.25em;
			padding-right: 1.25em;
			position: absolute;
			right: 0;
			text-align: right;
			top: 0;
			vertical-align: middle;
			width: 7em;
		}

			#menu .close:before {
				-moz-osx-font-smoothing: grayscale;
				-webkit-font-smoothing: antialiased;
				font-family: FontAwesome;
				font-style: normal;
				font-weight: normal;
				text-transform: none !important;
			}

			#menu .close:before {
				content: '\F00D';
				font-size: 1.25em;
			}

			#menu .close:hover {
				color: #484459;
			}

			@media screen and (max-width: 736px) {

				#menu .close {
					height: 4em;
					line-height: 4em;
				}

			}

		#menu.visible {
			-webkit-transform: translateX(0);
			transform: translateX(0);
			box-shadow: 0 0 4em 0 rgba(0, 0, 0, 0.1);
			visibility: visible;
		}

		@media screen and (max-width: 736px) {

			#menu {
				padding: 2.5em 1.75em;
			}

		}

/* Banner */

	#banner {
		padding: 8em 0 5.6em 0 ;
		text-align: center;
	}

		#banner header {
			padding: 0;
			overflow: hidden;
		}

			#banner header > * {
				-webkit-transition: opacity 1s ease-in-out, -webkit-transform 0.65s ease-out, -webkit-filter 0.65s ease-out;
				transition: opacity 1s ease-in-out, -webkit-transform 0.65s ease-out, -webkit-filter 0.65s ease-out;
				transition: opacity 1s ease-in-out, transform 0.65s ease-out, filter 0.65s ease-out;
				transition: opacity 1s ease-in-out, transform 0.65s ease-out, filter 0.65s ease-out, -webkit-transform 0.65s ease-out, -webkit-filter 0.65s ease-out;
				opacity: 1;
			}

			#banner header > :nth-child(1) {
				-webkit-transition-delay: 0s !important;
				transition-delay: 0s !important;
			}

			#banner header > :nth-child(2) {
				-webkit-transition-delay: 0.125s !important;
				transition-delay: 0.125s !important;
			}

			#banner header > :nth-child(3) {
				-webkit-transition-delay: 0.25s !important;
				transition-delay: 0.25s !important;
			}

		#banner .icon {
			display: block;
			font-size: 4em;
		}

		#banner h1 {
			font-size: 2.4em;
			margin-bottom: 0.7em;
			padding-bottom: 0.5em;
		}

			#banner h1:before {
				left: calc(50% - 1px);
			}

			#banner h1:after {
				left: calc(50% - 30px);
			}

		#banner p {
			font-size: 0.9em;
			margin-bottom: 3.15em;
		}

		#banner .button {
			min-width: 15em;
		}

		#banner .actions {
			-webkit-transition: opacity 1s ease-in-out, -webkit-transform 0.65s ease-in-out, -webkit-filter 0.85s ease-out;
			transition: opacity 1s ease-in-out, -webkit-transform 0.65s ease-in-out, -webkit-filter 0.85s ease-out;
			transition: opacity 1s ease-in-out, transform 0.65s ease-in-out, filter 0.85s ease-out;
			transition: opacity 1s ease-in-out, transform 0.65s ease-in-out, filter 0.85s ease-out, -webkit-transform 0.65s ease-in-out, -webkit-filter 0.85s ease-out;
			-webkit-transition-delay: 0.625s;
			transition-delay: 0.625s;
			opacity: 1;
		}

		body.is-loading #banner header > * {
			opacity: 0;
			-webkit-transform: scale(1.1);
			transform: scale(1.1);
			-moz-filter: blur(4px);
			-webkit-filter: blur(4px);
			-ms-filter: blur(4px);
			filter: blur(4px);
		}

		body.is-loading #banner .actions {
			opacity: 0;
			-webkit-transform: translateY(0.25em);
			transform: translateY(0.25em);
			-moz-filter: blur(2px);
			-webkit-filter: blur(2px);
			-ms-filter: blur(2px);
			filter: blur(2px);
		}

		@media screen and (max-width: 1680px) {

			#banner {
				padding: 8em 0 5.6em 0 ;
			}

		}

		@media screen and (max-width: 1280px) {

			#banner {
				padding: 6em 0 3.6em 0 ;
			}

		}

		@media screen and (max-width: 980px) {

			#banner {
				padding: 8em 2em 5.6em 2em ;
			}

		}

		@media screen and (max-width: 736px) {

			#banner {
				padding: 2em 2em 0.1em 2em ;
			}

				#banner h1 {
					font-size: 1.9em;
				}

		}

		@media screen and (max-width: 480px) {

			#banner {
				padding: 4em 2em 1.6em 2em ;
			}

		}

/* Main */

	#main {
		padding-top: 10em;
	}

		#main > .inner > .content {
			padding: 2em 3em 3em;
		}

		@media screen and (max-width: 1280px) {

